if ($.validator) {
    $.validator.setDefaults({
        errorPlacement: function(error, element) {
            var helper = element.next('.help-block')
            if(helper.length) {
                helper.data('html', helper.html()).html(error);
            } else {
                helper = $('<div class="help-block" />')
                helper.data('html', '').html(error);
            }
            helper.insertAfter(element);
            element.parent().addClass('has-error');
        },
        success: function(label, element) {
            var helper = label.parent('.help-block');
            helper.html(helper.data('html')).parent('.has-error').removeClass('has-error');
        }
    });
    $.extend( $.validator.messages, {
        required: "",
        remote: "",
        email: "",
        url: "",
        date: "",
        dateISO: " (YYYY-MM-DD)",
        number: "",
        digits: "",
        creditcard: "",
        equalTo: "",
        extension: "",
        maxlength: $.validator.format( " {0} " ),
        minlength: $.validator.format( " {0} " ),
        rangelength: $.validator.format( " {0}  {1} " ),
        range: $.validator.format( " {0}  {1} " ),
        max: $.validator.format( " {0} " ),
        min: $.validator.format( " {0} " )
    } );
}


